.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body{
  background-color: black;
}
.main_flex{
  display: flex;
  width: 100%;   /* Largura total da viewport (tela) */
  height: 100%;  /* Altura total da viewport (tela) */

}
.curriculosTitulo {
  margin-left: 250px; /* Margem para afastar os títulos do banner */
  font-size: 18px; /* Ajuste o tamanho da fonte se necessário */
  text-align: left; /* Centralize o texto, se for desejado */
  color: white; /* Cor ajustada */
}
.curriculosDiv{
  margin-top: -110px;
}
